.profile {
  margin: 40px auto 0;
  max-width: 880px;
  display: flex;
  flex-direction: row;  
}
@media (max-width: 1069px) {
  .profile {
    margin: 42px auto 0;
    flex-direction: column;
    align-items: center;
  }
}
@media (max-width: 767px) {
  .profile {
    margin: 42px 19px 0;
  }
}